@import url(https://fonts.googleapis.com/css2?family=Vesper+Libre&family=Viaoda+Libre&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.primary-text{
  font-family: 'Viaoda Libre', cursive ;
}

.name{
  font-size: 30px;
  text-decoration: underline;
}

.secondary-text{
  font-family: 'Vesper Libre', serif;
}

.card{
  width: 450px;
  height: 475px;
  border-radius: 15px;
  box-shadow:
  0 0.4px 2.2px rgba(0, 0, 0, 0.02),
  0 1px 5.3px rgba(0, 0, 0, 0.028),
  0 1.9px 10px rgba(0, 0, 0, 0.035),
  0 3.4px 17.9px rgba(0, 0, 0, 0.042),
  0 6.3px 33.4px rgba(0, 0, 0, 0.05),
  0 15px 80px rgba(0, 0, 0, 0.07)
;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.profile{
    padding-top: 15px;
}

.img-stats-links{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.name-stats-links{
    margin-left: 5px;
    margin-top: 25px;
}

.user-img{
    height: 135px;
    width: 135px;
    margin-left: 35%;
    border-radius: 70px;
}

.stats{
    display: flex;
    font-size: 14px;
    justify-content: center;
}

.stat{
    margin-left: 10px;
}

.bio{
    display: flex;
    flex-direction: column;
    text-align: left;
    margin-left: 15px;
    margin-top: 15px;
}

.name{
    margin: 0;
}

a{
    height: 35px;
    width: 35px;
    margin-left: 20px;
    color: white;
}

a:visited{
    color: white;
}

#bio-header{
    font-size: 20px;
    font-weight: 500;
    margin: 0;
}

.icon{
    height: 30px;
    width: 30px;
}
